import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { FormattedMessage } from 'react-intl'
import { useLocation } from '@reach/router'

import { fetchNews } from '../news/newsSlice'
import { selectBookmarksByPage, selectPagination } from './bookmarksSlice';
import { ItemExcerpt } from '../news/ItemExcerpt'
import { PageLink } from '../../components/PageLink'
import './BookmarksList.scss';

export const BookmarksList = () => {
  const location = useLocation()
  const searchParams = new URLSearchParams(location.search)
  const page = parseInt(searchParams.get('p') || 0)
  const query = searchParams.get('q')

  const dispatch = useDispatch()
  const bookmarks = useSelector(state => selectBookmarksByPage(state, { query, page }))
  const [hasMore, hasPrevious] = useSelector(state => selectPagination(state, { query, page }))

  const itemStatus = useSelector(state => state.news.status)
  const error = useSelector(state => state.news.error)

  useEffect(() => {
    if (itemStatus === 'idle') {
      dispatch(fetchNews())
    }
  }, [itemStatus, dispatch])

  let content

  if (itemStatus === 'loading') {
    content = <div className="loader"><FormattedMessage id="loading" defaultMessage="Loading..." /></div>
  } else if (itemStatus === 'succeeded') {
    content = (
      <div>
        <div className="BookmarksList-list flex-row">
          {bookmarks.length ? bookmarks.map(item => (
            <div key={item.id} className="flex-small one-fourth">
              <ItemExcerpt item={item} />
            </div>
          )) : <div className="BookmarksList-empty">
            <FormattedMessage id="empty" defaultMessage="Empty" />
          </div>}
        </div>
        <div className="BookmarksList-pagination">
          <PageLink disabled={!hasPrevious} page={page - 1}>
            <FormattedMessage id="previous"
              defaultMessage="Previous"
              description="Link"
            />
          </PageLink>
          <PageLink disabled={!hasMore} page={page + 1}>
            <FormattedMessage id="next"
              defaultMessage="Next"
              description="Link"
            />
          </PageLink>
        </div>
      </div>
    )
  } else if (itemStatus === 'failed') {
    content = <div>{error}</div>
  }

  return (
    <section className="BookmarksList">
      {content}
    </section>
  )
}