import {useEffect, useState} from 'react'
import { Link, useLocation, useNavigate } from '@reach/router'
import { FormattedMessage, useIntl } from 'react-intl'

import './Navbar.scss';

const NavLink = props => (
  <Link
    {...props}
    getProps={({ isCurrent }) => ({
      className: isCurrent ? 'active' : ''
    })}
  />
)

export const Navbar = () => {
  const location = useLocation()
  const searchParams = new URLSearchParams(location.search)
  const paramQuery = searchParams.get('q') || ''

  const [query, setQuery] = useState(paramQuery)

  const intl = useIntl()
  const navigate = useNavigate()

  const onQueryChanged = e => setQuery(e.target.value)
  useEffect(() => setQuery(paramQuery), [paramQuery])

  const handleSubmit = e => {
    e.preventDefault()

    if (query) {
      searchParams.set('q', query)
    } else {
      searchParams.delete('q')
    }
    searchParams.delete('p')

    navigate(searchParams.entries().next().done ? location.pathname : `?${searchParams}`)
  }

  return (
    <div className="Navbar flex-row">
      <nav className="Navbar-links flex-small">
        <div>
          <NavLink to="/">
            <FormattedMessage id="news"
              defaultMessage="News"
              description="Link"
            />
          </NavLink>
        </div>
        <div>
          <NavLink to="bookmarks">
            <FormattedMessage id="bookmarks"
              defaultMessage="Bookmarks"
              description="Link"
            />
          </NavLink>
        </div>
      </nav>
      <section className="flex-small Navbar-search">
        <form onSubmit={handleSubmit}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 13 13">
            <path fillRule="evenodd" d="M.9 5.37a4.37 4.37 0 118.73 0 4.37 4.37 0 01-8.73 0zm7.64 3.81l3.3 3.12-3.3-3.12z" fill="none" stroke="#fff" strokeWidth="2"/>
          </svg>
          <input
            type="search"
            placeholder={intl.formatMessage({
              id: 'search',
              defaultMessage: 'Search',
              description: 'Placeholder',
            })}
            value={query}
            onChange={onQueryChanged}
          />
        </form>
      </section>
    </div>
  )
}