import { createAsyncThunk, createEntityAdapter, createSelector, createSlice } from '@reduxjs/toolkit'

import { client } from '../../api/client'
import { makeSelectByQuery } from '../../selectors/query'
import { makeSelectByPage } from '../../selectors/page'

const newsAdapter = createEntityAdapter({
  sortComparer: (a, b) => b.datetime - a.datetime,
})

const initialState = newsAdapter.getInitialState({
  status: 'idle',
  error: null,
})

export const fetchNews = createAsyncThunk('news/fetchNews', () =>
  client.get('https://finnhub.io/api/v1/company-news?symbol=AAPL&from=2020-01-01'))

const newsSlice = createSlice({
  name: 'news',
  initialState,
  reducers: {},
  extraReducers: {
    [fetchNews.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchNews.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      // Add any fetched news to the array
      newsAdapter.upsertMany(state, action.payload)
    },
    [fetchNews.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
  },
})

export default newsSlice.reducer

// Export the customized selectors for this adapter using `getSelectors`
export const {
  selectAll: selectAllNews,
  selectById: selectItemById,
  selectIds: selectItemIds,
} = newsAdapter.getSelectors(state => state.news)

const selectNewsWithoutFirst = createSelector(
  selectAllNews,
  news => news.slice(1)
)

const selectNewsByQuery = makeSelectByQuery(selectNewsWithoutFirst)

const [selectNewsByPage, selectPagination] = makeSelectByPage(selectNewsByQuery, 6)

export { selectNewsByPage, selectPagination }