import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { FormattedMessage } from 'react-intl'
import { useLocation } from '@reach/router'

import { fetchNews, selectAllNews, selectNewsByPage, selectPagination } from './newsSlice'
import { ItemExcerpt } from './ItemExcerpt'
import { PageLink } from '../../components/PageLink'
import './NewsList.scss';

export const NewsList = () => {
  const location = useLocation()
  const searchParams = new URLSearchParams(location.search)
  const page = parseInt(searchParams.get('p') || 0)
  const query = searchParams.get('q')

  const dispatch = useDispatch()
  const firstItem = useSelector(state => selectAllNews(state)[0])
  const news = useSelector(state => selectNewsByPage(state, { query, page }))
  const [hasMore, hasPrevious] = useSelector(state => selectPagination(state, { query, page }))

  const itemStatus = useSelector(state => state.news.status)
  const error = useSelector(state => state.news.error)

  useEffect(() => {
    if (itemStatus === 'idle') {
      dispatch(fetchNews())
    }
  }, [itemStatus, dispatch])

  let content

  if (itemStatus === 'loading') {
    content = <div className="loader"><FormattedMessage id="loading" defaultMessage="Loading..." /></div>
  } else if (itemStatus === 'succeeded') {
    content = (
      <div>
        <div className="flex-row">
          <div className="NewsList-last flex-small one-third">
            <ItemExcerpt item={firstItem} isLast />
          </div>
          <div className="NewsList-list flex-small two-thirds flex-row">
            {news.length ? news.map(item => (
              <div key={item.id} className="flex-large one-third">
                <ItemExcerpt item={item} />
              </div>
            )) : <div className="NewsList-not-found">
              <FormattedMessage id="not-found" defaultMessage="Nothing Found" />
            </div>}
          </div>
        </div>
        <div className="NewsList-pagination">
          <PageLink disabled={!hasPrevious} page={page - 1}>
            <FormattedMessage id="previous"
              defaultMessage="Previous"
              description="Link"
            />
          </PageLink>
          <PageLink disabled={!hasMore} page={page + 1}>
            <FormattedMessage id="next"
              defaultMessage="Next"
              description="Link"
            />
          </PageLink>
        </div>
      </div>
    )
  } else if (itemStatus === 'failed') {
    content = <div>{error}</div>
  }

  return (
    <section className="NewsList">
      {content}
    </section>
  )
}