import React from 'react'
import clsx from 'clsx';
import { useDispatch, useSelector } from 'react-redux'
import { useIntl } from 'react-intl';

import { bookmarkTriggered } from './bookmarksSlice'
import './BookmarkButton.scss';

export const BookmarkButton = ({ itemId }) => {
  const dispatch = useDispatch()
  const intl = useIntl()

  const added = useSelector(state => state.bookmarks.includes(itemId))

  const classname = clsx('BookmarkButton', {
    added,
  })

  return (
    <button
      type="button"
      title={intl.formatMessage({
        id: 'add-to-bookmarks',
        defaultMessage: 'Add to Bookmarks',
        description: 'Hint',
      })}
      className={classname}
      onClick={() =>
        dispatch(bookmarkTriggered(itemId))
      }
    >
      <svg xmlns="http://www.w3.org/2000/svg" width="12" height="13">
        <path d="M0 0h11.5v12.3L5.7 10 0 12.3V0z" />
      </svg>
    </button>
  )
}