import { createSelector } from '@reduxjs/toolkit'
import escapeStringRegexp from 'escape-string-regexp'

export const makeSelectByQuery = selectItems => createSelector(
  [selectItems, (state, { query }) => query],
  (news, query) => {
    if (query) {
      const re = new RegExp(escapeStringRegexp(query), 'i')
      news = news.filter(item => re.test(item.headline) || re.test(item.summary))
    }
    return news
  }
)