import { createSelector } from '@reduxjs/toolkit'

export const makeSelectByPage = (selectItems, perPage) => [
  createSelector(
    [selectItems, (state, { page }) => page],
    (news, page) => {
      const startIndex = page * perPage
      return news.slice(startIndex, startIndex + perPage)
    }
  ),
  createSelector(
    [selectItems, (state, { page }) => page],
    (news, page) => [Math.ceil(news.length / perPage) - 1 > page, page > 0]
  )
]