import { createSelector, createSlice } from '@reduxjs/toolkit'
import { makeSelectByQuery } from '../../selectors/query'
import { makeSelectByPage } from '../../selectors/page'

const initialState = []

const bookmarksSlice = createSlice({
  name: 'bookmarks',
  initialState,
  reducers: {
    bookmarkTriggered(state, action) {
      const itemId = action.payload
      const index = state.indexOf(itemId)
      if (index === -1) {
        state.push(itemId)
      } else {
        state.splice(index, 1)
      }
    },
  },
})

export const { bookmarkTriggered } = bookmarksSlice.actions

export default bookmarksSlice.reducer

export const selectBookmarked = createSelector(
  [state => state.news.entities, state => state.bookmarks],
  (news, bookmarks) => bookmarks.map(itemId => news[itemId])
)

const selectBookmarksByQuery = makeSelectByQuery(selectBookmarked)

const [selectBookmarksByPage, selectPagination] = makeSelectByPage(selectBookmarksByQuery, 8)

export { selectBookmarksByPage, selectPagination }