import { FormattedDate, FormattedMessage } from 'react-intl';

import { BookmarkButton } from '../bookmarks/BookmarkButton';
import './ItemExcerpt.scss';

export const ItemExcerpt = ({ item, isLast }) =>
  (
    <article className="ItemExcerpt">
      <div className="ItemExcerpt-related">{item.related}</div>
      {isLast && <div className="ItemExcerpt-last">
        <FormattedMessage id="latest-news"
          defaultMessage="Latest News"
          description="Label" />
      </div>}
      <div className="ItemExcerpt-title">
        <h3>{item.headline}</h3>
        <p>{item.summary}</p>
      </div>
      <div className="ItemExcerpt-info">
        {isLast && <div className="ItemExcerpt-read-news">
          <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20">
            <path d="M10.5 20a9.6 9.6 0 110-19.3 9.6 9.6 0 010 19.2z" />
            <path d="M6.5 10.6h6.9" />
            <path d="M11.8 8l2.3 2.4-2.3 2.5" />
          </svg>
          <FormattedMessage id="read-news"
            defaultMessage="Read News"
            description="Link" />
        </div>}
        <div className="ItemExcerpt-datetime">
          <FormattedDate value={item.datetime * 1000} day="2-digit" month="short" />
        </div>
        <div className="ItemExcerpt-min-read">
          <FormattedMessage id="n-min-read" defaultMessage="{n} min read" values={{ n: 6 }} />
        </div>
      </div>
      <div className="ItemExcerpt-buttons">
        <svg className="ItemExcerpt-link" xmlns="http://www.w3.org/2000/svg" width="11" height="14">
          <path d="M1.5 12.2v-5l7.6-5" />
          <path d="M6.3 1.7h3V5" />
        </svg>
        <BookmarkButton itemId={item.id} />
      </div>
      <a
        href={item.url}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img src={item.image} alt={item.headline} />
      </a>
    </article>
  )