import { Link, useLocation } from '@reach/router'

export const PageLink = ({ page, disabled, ...other }) => {
  const location = useLocation()

  if (disabled) {
    return <button disabled className="round-button" {...other} />
  }

  const searchParams = new URLSearchParams(location.search)
  if (page > 0) {
    searchParams.set('p', String(page))
  } else {
    searchParams.delete('p')
  }
  const to = searchParams.entries().next().done ? location.pathname : `?${searchParams}`

  return <Link to={to} className="round-button button" {...other} />
}